import dyDatePicker from '../../common/dy-Data.vue'
export default {
	data() {
		return {
			time: '',
			fll: true,
			date1: '',
			mydate: '',
			mydate1: '',
			times: '',
			transfer: '0',
			sallnum: '0',
			scrollTop: 0,
			old: {
				scrollTop: 0,
			},
		};
	},
	components: {
		dyDatePicker
	},
	onLoad() {
		var that = this;
		var date = new Date;
		var year = date.getFullYear();
		var month = date.getMonth() + 1;
		month = (month < 10 ? "0" + month : month);
		that.mydate = (year.toString() + '年' + month.toString() + '月');
		that.mydate1 = (year.toString() + '-' + month.toString());
		console.log(that.mydate1)
		var that = this;
		uni.request({
			url: this.url + 'usermachine/recode/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + uni.getStorageSync('token')
			},
			data: {
				month: that.mydate1
			},
			success(res) {
				console.log(res)
				that.time = res.data.data;
				that.sallnum = res.data.sallnum
				that.transfer = res.data.transfer
				that.time = that.time.reverse();
				console.log(res.data)
				if (that.time.length == 0) {
					that.fll = false;
				} else {
					that.fll = true;
				}
			}
		});
	},
	methods: {
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		DateChang(e) {
			var that = this;
			that.mydate = e
			uni.request({
				url: this.url + 'usermachine/recode',
				method: 'GET',
				header: {
					Authorization: 'JWT' + ' ' + uni.getStorageSync('token')
				},
				data: {
					month: e
				},
				success(res) {
					console.log(res.data.sallnum)
					console.log(res.data.transfer)
					that.time = res.data.data;
					that.times = res.data
					that.sallnum = res.data.sallnum
					that.transfer = res.data.transfer
					that.time = that.time.reverse();
					console.log(res.data)
					if (that.time.length == 0) {
						that.fll = false;
					} else {
						that.fll = true;
					}
				}
			});
		}
	},
	filters: {
		formatDate(n) {
			n = n.substr(5, 11)
			n = n.replace('-', '月').replace(' ', '日 ')
			return n
		}
	},
	
	
};
